
.scene {
    position: relative;
    width: 284px;
    height: 258px;
}
.scene:after {
  content: '';
  display: table;
  clear: both;
}
.pillar {
    display: inline-block;
    float: left;
    height: 135px;
    margin-bottom: 15px;
    position: absolute;
}
@media screen and (max-width: 640px) and (orientation: landscape) {
.pillar {
        height: 120px;
}
.scene {
        width: 280px;
        height: 235px;
}
}
.pillar:nth-child(1) {
    top: 60px;
    z-index: 4;
}
.pillar:nth-child(2) {
    top: 45px;
    left: 30px;
    z-index: 3;
}
.pillar:nth-child(3) {
    top: 30px;
    left: 60px;
    z-index: 2;
}
